import React from "react"
import TopLeftLink from "./topLeftLink"
import TopRightLink from "./topRightLink"
import styles from "../styles/toolbar.module.css"
import MediaQuery from "react-responsive"
import Menu from "./menu"

export default class Toolbar extends React.Component {
    constructor(props){
        super(props);
        const color = (props.color) ? (props.color) : "#000000"
        this.state = {color: color}
    }

    render() {
        return (<div className={styles.header}>
            <TopLeftLink to="/" color={this.state.color}>General Futuristics</TopLeftLink>
            <MediaQuery minWidth={840}>
                <TopRightLink to="/about/" classname={styles.about} color={this.state.color}>About</TopRightLink>
                <TopRightLink to="/work/" classname={styles.work} color={this.state.color}>Work</TopRightLink>
                <TopRightLink to="/inephemera/" classname={styles.inephemera}
                              color={this.state.color}>InEphemera</TopRightLink>
                <TopRightLink to="/team/" classname={styles.contact} color={this.state.color}>Contact</TopRightLink>
            </MediaQuery>
            <MediaQuery maxWidth={840}><Menu/></MediaQuery>
        </div>);
    }
}

export const WhiteToolbar = () => (<Toolbar color="#FFFFFF"/>)

