import React from "react"
import styles from "../styles/thoughts.module.css"

export default (props) => (
    <div className={`${styles.thoughts} ${props.classname}`}>
        <TextChange/>
        <div style={{width: "104px", height: "32px", top: "17px", color: "#C0C0C0"}}>Let’s talk.</div>
    </div>
)

class TextChange extends React.Component {
    constructor (props) {
        super(props);
        this.state = {
            word: ""
        }
        // this.word = "artificial intelligence"
        this.changeText = this.changeText.bind(this);
        // setTimeout(this.changeText, 3000);
        this.update = this.update.bind(this);
        this.array = [[
            "artificial intelligence",
            "synthetic biology",
            "computer vision",
            "nuclear proliferation"],
            ["cyberwarfare",
            "cloud computing",
            "robotics",
            "extraplanetary exploration"],
            ["the microbiome",
            "privacy",
            "climate change",
            "the water crisis"],
            ["surveillance capitalism",
            "brain-computer interfaces",
            "nootropics",
            "prosthetics"],
            ["philanthrocapitalism",
            "addictive design",
            "quantum computing",
            "the right to be forgotten"]
        ]
        this.i = -1;
        this.arrayCount = -1;
        this.changeText();
    }

    /*
    setting state causes a re-render

    */

    // identify which word to display (loop through pool of arrays,
    // and randomly select a word from that pool.
    // setState causes a re-render
    changeText(){
        const index = Math.floor(Math.random() * 4);
        this.i = -1; //reset counter
        this.arrayCount = (this.arrayCount + 1) % 5
        const word = this.array[this.arrayCount][index]
        this.setState({word: word, timeout: (150), text: ""});
    }

    //called the first time the page is rendered
    componentDidMount() {
        setTimeout(this.update, this.state.timeout);
    }

    //called every time after the first the page is rendered
    //if either the word to display has changed, or
    //if another letter in the word has displayed,
    //update the state.
    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevState.word !== this.state.word){
            setTimeout(this.update, this.state.timeout);
        }
        else if (prevState.text !== this.state.text) {
            setTimeout(this.update, this.state.timeout);
        }
    }

    //update the state to either change the word,
    //or display the next letter in the word.
    //setState causes a re-render.
    update(){
        if (this.i < this.state.word.length - 1){
            this.i += 1;
            this.setState({text: this.state.text + this.state.word.charAt(this.i)});
            // this.render();
        }
        else{
            setTimeout(this.changeText, 1500);
        }
    }

    render(){
        let span;
        if (this.i < this.state.word.length - 2){
            span = <span className={styles.blinkCursor}>|</span>
        }
        else{
            span = null;
        }
        return (
            <div style={{width: "400px", height: "32px"}}>
                Thoughts on {this.state.text}
                {span}
                <span>?</span>
            </div>
        )
    }
}