const MenuOverlay = () => (
    <div className={styles.menuOverLay}>
        <div style={{position: "absolute", height: "0px"}}/>
        <Link to="/about/" className={styles.menuOption}>About</Link>
        <Link to="/work/" className={styles.menuOption}>Work</Link>
        <Link to="/inephemera/" className={styles.menuOption}>Inephemera</Link>
        <Link to="/contact/" className={styles.menuOption}>Contact</Link>
    </div>
)

//try having two different classes, one for cross one for menu
export default class Fade extends React.Component{
    constructor(props){
        super(props)
        this.state = {
            spin: false,
            clicked: false
        }
        this.spinDone = this.spinDone.bind(this)
        this.img = props.img
        this.handleClick = this.handleClick.bind(this)
    }
    componentDidMount () {
        const elm = this.refs.button
        elm.addEventListener('animationend', this.spinDone)
    }
    componentWillUnmount () {
        const elm = this.refs.button
        elm.removeEventListener('animationend', this.spinDone)
    }

    spinDone () {
        this.setState({spin: false})
    }

    handleClick(){
        this.setState({spin: true})
        this.setState({checked: !this.state.checked})
    }
    render () {
        let img;
        let overlay;
        if (this.state.checked){
            img = crossIcon
            overlay = <MenuOverlay/>
        }
        else{
            img = menuIcon
            overlay = null;
        }
        const spin = this.state.spin

        return (
            <div className={styles.menu} onClick={this.handleClick}>
                <img src={img}
                    alt=""
                    ref='button'
                    className={spin ? `${styles.spin}` : ''}>
                    {this.children}
                </img>
                {overlay}
            </div>
        )
    }
}

